import React, { Component } from "react";
import { Input, Button, Table, Pagination, message } from "antd";
import Opertion from "../component/operation"; //
import { importXlsx, addDalist, deleteDalist } from "@/api/management.js";
import { bindActionCreators } from "redux";
import columns from "@/config/management/columns.js";//
import * as allAction from "@/store/management/management.action";
import { connect } from "react-redux";
class Archives extends Component {
  state = {
    pageid: 0,
    limit: 10,
    //
    edit: false,
    //start
    staffName: "",
    department: "",
    position: "",
    tel: "",
    address: "",
    serachVal: "",
    //end
    //
    checkedarr: [],
  };
  render() {
    let {
      checkedarr,
      edit,
      staffName,
      department,
      position,
      tel,
      address
    } = this.state;
    let { alllist, allcount } = this.props;
    //
    const rowSelection = {
      onChange: selectedRowKeys => {
        this.setStateCheckarr(selectedRowKeys);
      }
    };
    return (
      <div className="yggl_wrap">
        <h2></h2>
        <div className="yggl_serch">
          <Input
            size="small"
            placeholder=" "
            onInput={e => {
              this.setState({
                serachVal: e.target.value
              });
            }}
          />
          <Button onClick={() => {}}></Button>
        </div>
        {/* start  */}
        <Opertion
          showDelbtn={this.showDelbtn.bind(this)}
          addfn={this.addfn.bind(this)}
          closes={this.closes.bind(this)}
          importXls={this.importFn.bind(this)}
          handleDelete={this.handleDelete.bind(this)}
          edit={edit}
          checkedarr={checkedarr}
          staffName={staffName}
          department={department}
          position={position}
          tel={tel}
          address={address}
        >
          {/* start  */}
          <div>
            <span></span>
            <Input
              value={staffName}
              onChange={e => this.setState({ staffName: e.target.value })}
              type="text"
              placeholder=""
            />
            <span></span>
            <Input
              value={department}
              onChange={e => this.setState({ department: e.target.value })}
              type="text"
              placeholder=""
            />
            <span></span>
            <Input
              value={position}
              onChange={e => this.setState({ position: e.target.value })}
              type="text"
              placeholder=""
            />
            <span></span>
            <Input
              value={tel}
              onChange={e => this.setState({ tel: e.target.value })}
              type="text"
              placeholder=""
            />
            <span></span>
            <Input
              value={address}
              onChange={e => this.setState({ address: e.target.value })}
              type="text"
              placeholder=""
            />
          </div>
          {/* end  */}
        </Opertion>
        {/* end  */}
        <div className="yggl_list">
          <Table
            columns={columns}
            pagination={false}
            dataSource={alllist.length ? alllist : null}
            bordered
            size="middle"
            rowSelection={edit ? rowSelection : null}
          />
        </div>
        <Pagination
          defaultCurrent={1}
          total={allcount}
          onChange={this.pageChange}
        ></Pagination>
      </div>
    );
  }
  componentDidMount() {
    let { pageid, limit } = this.state;
    this.getall(pageid, limit);
  }
  //
  getall(pageid, limit) {
    this.props.getDatalist(pageid, limit);
  }
  //
  dellist() {
    this.setState({
      staffName: "",
      department: "",
      position: "",
      tel: "",
      address: ""
    });
  }
  //
  async addfn() {
    let {
      staffName,
      department,
      position,
      tel,
      address,
      pageid,
      limit
    } = this.state;
    let res = await addDalist({
      staffName,
      department,
      position,
      tel,
      address
    });
    if (res.code) {
      message.success("");
      await this.getall(pageid, limit);
      this.dellist();
    } else {
      message.error("");
    }
  }
  //
  closes() {
    this.dellist();
  }
  //,
  showDelbtn() {
    this.setState({ edit: !this.state.edit });
    //
    if (this.state.edit) {
      this.setState({ checkedarr: [] });
    }
  }
  //idcheckbox
  setStateCheckarr(arr) {
    this.setState({ checkedarr: arr });
  }
  //
  importFn(file) {
    let { pageid, limit } = this.state;
    importXlsx(file).then(res => {
      this.props.importList(res.data);
      this.getall(pageid, limit);
    });
  }
  //
  async handleDelete() {
    let {checkedarr, pageid, limit} = this.state;
    if (!checkedarr.length) {
      message.warning("");
      return;
    }
    let res = await deleteDalist({ id: JSON.stringify(checkedarr) });
    if (res.code) {
      message.success("");
      //,
      await this.props.del(checkedarr);
      this.setState({ checkedarr: []});
      //10
      if (this.props.alllist.length!==limit) {
          //
        if (!this.props.alllist.length) {
          if(pageid === 0){
            this.setState({ pageid: pageid });
          }else{
            this.setState({ pageid: pageid-- });
          }
        }
        this.setState({ pageid: pageid });
        this.getall(pageid, limit);
      }
    }
  }
  //
  pageChange = page => {
    this.setState({ pageid: page - 1 }, () => {
      let { pageid, limit } = this.state;
      this.getall(pageid, limit);
    });
  };
}

export default connect(
  state => {
    return state.management;
  },
  dispatch => bindActionCreators(allAction, dispatch)
)(Archives);
