import React from "react"
export default [
    {
      title: "",
      dataIndex: "staffName",
      className: "listOne"
    },
    {
      title: "",
      dataIndex: "staffId"
    },
    {
      title: "",
      dataIndex: "department"
    },
    {
      title: "",
      dataIndex: "position"
    },
    {
      title: "",
      dataIndex: "salary"
    },
    {
      title: "",
      dataIndex: "tel"
    },
    {
      title: "",
      dataIndex: "isQuit",
      render: (text, item) => <span>{item.isQuit === "0" ? "" : ""}</span>
    },
    {
      title: "",
      dataIndex: "quitReason"
    },
    {
      title: "",
      dataIndex: "IDNum"
    },
    {
      title: "",
      dataIndex: "entryTime",
      render: (text, item) => (
        <span>
          {new Date(Number(item.entryTime))
            .toLocaleDateString()
            .replace(/\//g, "-")}
        </span>
      )
    },
    {
      title: "",
      dataIndex: "education"
    },
    {
      title: "",
      dataIndex: "nativePlace"
    },
    {
      title: "",
      dataIndex: "nation"
    },
    {
      title: "",
      dataIndex: "isMarray",
      render: (text, item) => (
        <span>{item.isMarray === "0" ? "" : ""}</span>
      )
    },
    {
      title: "",
      dataIndex: "nativeType"
    },
    {
      title: "",
      dataIndex: "address"
    },
    {
      title: "",
      dataIndex: "classId"
    },
    {
      title: "",
      dataIndex: "gartenId"
    },
    {
      title: "",
      dataIndex: "classNum"
    }
  ]